.button {
	font-family: 'Oswald', sans-serif;
	font-weight: 500;
	font-size: 16px;
	text-transform: uppercase;
	padding: 20px 70px;
	background-color: var(--c-primary);
	border-radius: 50px;
	outline: none;
	position: relative;
	transition: all 0.4s linear;
	&-m{
		padding: 15px 55px;
	}
	&-panel{
		display: inline-block;
		margin: 2rem auto 3rem;
		padding: 15px 55px;
	}
	span{
		position: relative;
		top: -1px;
	}
	&::after{
		pointer-events: none;
		content: "";
		position: absolute;
		top: 120%;
		left: 0;
		height: 100%;
		width: 100%;
		filter: blur(1.2em);
		opacity: .7;
		transform: perspective(1.5em) rotateX(35deg) scale(1, .6);
		transition: 0.4s linear;
	}
	&-click{
		overflow: hidden;
		border: 1px solid var(--c-primary);
		i{
			position: absolute;
			background-color: #e9f5f9;
			transform: translate(-50%,-50%);
			pointer-events: none;
			border-radius: 50%;
			animation: anim 1.2s linear infinite;
		}
		b{
			&:first-child{
				position: absolute;
				background-color: #e9f5f9;
				transform: translate(-50%,-50%);
				pointer-events: none;
				border-radius: 50%;
				animation: anim2 1.3s linear infinite;
				top: 50%;
				left: 50%;
			}
			// &:last-child{
			// 	position: absolute;
			// 	background-color: #e9f5f9;
			// 	transform: translate(-50%,-50%);
			// 	pointer-events: none;
			// 	border-radius: 50%;
			// 	animation: anim2 1.3s linear infinite;
			// 	// animation-delay: 0.4s;
			// 	top: 50%;
			// 	left: 0%;
			// }
		}
		s{
			clip-path: polygon(60% 0, 100% 0%, 100% 100%, 60% 100%, 40% 53%);
			background-color: #fff;
			overflow: hidden;
			width: 100%;
			display: flex;
			height: 100%;
			position: absolute;
			top: 0;
			left: 100%;
			transition: 0.6s linear;
		}
		span{
			z-index: 2;
		}
		&:hover{
			s{
				clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%, 0 50%);
				left: 0%;
			}
			color: var(--c-primary);
		}
	}
	
	@keyframes anim {
		0%{
			width: 0;
			height: 0;
			opacity: 0.5;
		}
		100%{
			width: 500px;
			height: 500px;
			opacity: 0.2;
		}
	}
	@keyframes anim2{
		0%{
			width: 0;
			height: 0;
			opacity: 0.5;
		}
		70%{
			width: 300px;
			height: 300px;
			opacity: 0.2;
		}
		100%{
			opacity: 0;
		}
	}
	
	&:hover {
		color: var(--c-primary);
		// background-color: var(--c-white);
		// box-shadow: 0 0px 40px 5px var(--c-primary), 0 0px 40px var(--c-primary), inset 0 0 0px 0px var(--c-primary);

		&::after{
			
			background-color: var(--c-white);
		}
	}
	

}

@media (max-width: 991px) {
	.button{
		font-size: 22px;
		padding: 0.5em 2.5em;
	}
}
@media (max-width: 768px) {
	.button{
		font-size: 18px;
		padding: 0.5em 2.5em;
	}
}
.slider_inner-btn{
	position: relative;
    width: 65px;
    height: 65px;
    outline: none;
    border: none !important;
    background-color: #33292c !important;
    border-radius: 50%;
    transition: transform 0.2s linear, background 0.2s linear, box-shadow 0.2s linear;
	i{
		font-size: var(--fz-s);
		color: #fff;
	}
	&::after{
		content: none;
	}
	&:hover {
                    
		&{
			color: var(--c-primary);
			background-color: var(--c-white) !important;
			box-shadow: 0 0px 40px 5px var(--c-primary), 0 0px 40px var(--c-primary), inset 0 0 0px 0px var(--c-primary);
		}
		i{
			color: var(--c-primary)
		}
		
	}
	@media (max-width: 991px) {
		width: 45px;
		height: 45px;
	}
}


.new-button{
	position: relative;
	display: inline-block;
	width: fit-content;
	width: 300px ;
	height: 70px;
	border-radius: 50px;	
	&-container{
		background-color: var(--c-primary);
		width: 300px ;
		height: 70px;
		border-radius: 50px;
		overflow: hidden;
		border: 1px solid var(--c-primary);
	}
	span{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		color: #fff;
		background-color: var(--c-primary);
		display: flex;
		align-items: center;
		justify-content: center;
		text-transform: uppercase;
		font-weight: 700;
		font-size: 16px;
	}
	span:nth-child(2){
		color: var(--c-primary);
		background-color: #fff;
		overflow: hidden;
		z-index: 2;
		transition: 0.4s linear;
		clip-path: polygon(60% 0, 100% 0%, 100% 100%, 60% 100%, 40% 53%);
		&:hover{
			clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%, 0 50%);
		}
	}
	span:nth-child(1):hover ~ span:nth-child(2){
		clip-path: polygon(100% 0, 100% 0%, 100% 100%, 100% 100%, 100% 50%);
	}
}